import React from 'react';
import { observer } from 'mobx-react';
import { Button, Card, CardContent } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { useStores } from '../Stores';
import HeaderMenu from '../Components/HeaderMenu';

const Dashboard = observer(() => {
    const { userStore } = useStores();
    const navigate = useNavigate();

    if (!userStore.user) {
        navigate('/login');
        return null;
    }

    const role = userStore.user.role;

    return (
        <>
        <HeaderMenu headerText={"Bienvenue " + userStore.user?.firstName}  />

        <div style={{padding: 20}}>

            <h3 className='dashboard-title'>
                Tableau de bord
            </h3>
            
            <section className='dahshboard-section'>
                <h1>
                    Vous etes: ({role})
                </h1>

                <div>
                    {role === 'OWNER' && (
                        <div>
                            <Card>
                                <CardContent>
                                    <h3>Gestion des modles</h3>
                                    <Button 
                                        variant="contained" 
                                        fullWidth onClick={() => navigate('/models')}
                                        startIcon={
                                            <img 
                                                src="/elements/manage.svg" 
                                                alt="Crer" 
                                                height={20} 
                                                width={20} 
                                                style={{ filter: 'invert(1)' }}
                                            />
                                        }
                                    >
                                        Grer les modles
                                    </Button>
                                </CardContent>
                            </Card>
                            <Card>
                                <CardContent>
                                    <h3>Crer un nouveau modle</h3>
                                    <Button 
                                        variant="contained" 
                                        fullWidth onClick={() => navigate('/models/create')}
                                        startIcon={
                                            <img 
                                                src="/elements/edit.svg" 
                                                alt="Crer" 
                                                height={20} 
                                                width={20} 
                                                style={{ filter: 'invert(1)' }}
                                            />
                                        }
                                    >
                                        Crer modle
                                    </Button>
                                </CardContent>
                            </Card>
                        </div>
                    )}
                    
                    {role === 'TEAM LEADER' && (
                        <>
                            <div>
                                <Card>
                                    <CardContent>
                                        <h3>Grer l'quipe</h3>
                                        <Button 
                                            variant="contained" 
                                            fullWidth onClick={() => navigate('/team')}
                                        >
                                            Inviter des membres
                                        </Button>
                                    </CardContent>
                                </Card>
                            </div>
                            <div>
                                <Card>
                                    <CardContent>
                                        <h3>Sessions d'valuation</h3>
                                        <Button 
                                            variant="contained" 
                                            fullWidth onClick={() => navigate('/sessions')}
                                        >
                                            Lancer une session
                                        </Button>
                                    </CardContent>
                                </Card>
                            </div>
                        </>
                    )}
                    
                    {role === 'MEMBER' && (
                        <>
                            <div>
                                <Card>
                                    <CardContent>
                                        <h3>Rpondre aux questions</h3>
                                        <Button 
                                            variant="contained" 
                                            fullWidth onClick={() => navigate('/questions')}
                                        >
                                            Accder
                                        </Button>
                                    </CardContent>
                                </Card>
                            </div>
                            <div>
                                <Card>
                                    <CardContent>
                                        <h3>Rsultats des sessions</h3>
                                        <Button 
                                            variant="contained" 
                                            fullWidth onClick={() => navigate('/results')}
                                        >
                                            Voir les rsultats
                                        </Button>
                                    </CardContent>
                                </Card>
                            </div>
                        </>
                    )}
                </div>
            </section>
        </div>
        </>
    );
});

export default Dashboard;
